.coin-container .content {
    display: flex;
    flex-direction: column;
    max-width: 70%;
    background-color: #26272b;
    box-shadow: 0 0 12px #18191b;
    border-radius: 8px;
    margin: 1rem auto;
    padding: 0.7rem 1rem;
}

.coin-container .content.chart {
    margin-top: 100px;
    margin-bottom: 100px;
}

.rank-btn {
    border: 1px solid gold;
    box-shadow: 0 0 8px gold;
    border-radius: 8px;
    background-color: gold;
    padding: 0.2rem;
    color: black;
}

.info {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.info .coin-heading {
    display: flex;
    align-items: center;
    margin: 1rem 0;
}

.info .coin-price {
    display: flex;
    align-items: center;
    justify-content: center;
}

.info p {
    margin-right: 1rem;
}

table {
    margin: 0.5rem 0;
}

td, th {
    padding: 8px;
    text-align: center;
}

th {
    background-color: #333;
}

.stats {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    width: 100%;
}

.stats .row {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #808080;
    margin: 0.6rem 0;
    padding-bottom: 0.5;
}

.stats .row p {
    color : #d3d3d3;
}

.about h3 {
    margin: 1rem 0;
}

@media (max-width: 700px) {
    .coin-container .content {
        max-width: 100%;
        padding: 0.5rem;
        margin: 0.5rem;
    }

    .stats {
        grid-template-columns: 1fr;
    }

    td, th {
        padding: 3px;
    }
}